body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{
  min-height: 100vh !important;
}

.frontpageBackground{
    background-image: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3) ), url(/static/media/background.fa9d3e34.jpg);
    background-size: 100vw 100vh;
}

@media only screen and (max-width: 600px) {
    .frontpageBackground{
        background-size: 200vh;
    }
}
.customNavbar{
    padding-top: 0.5rem;
    font-weight: bold;
}

.customImage{
    height:25px;
    margin-top: 22%;
}
.saleStyle{
    color: yellow;
    font-size: 75%;
    margin-bottom: 0px;
}

.saleComponentStyle{
    padding-top:0px;
}

.itemMargin{
    margin-top: 2.2%;
}
.topnav {
    overflow: hidden;
    background-color: #ff6666;
    position: relative;
    font-weight: bold;
}

.topnav #links {
    display: none;
}

.topnav a {
    color: white;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    display: block;
}

.topnav a.icon {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
}

.active {
    background-color: #ff4c4c;
    color: white;
}

.mobileImage{
    height:25px;
}

.saleStyle{
    color: yellow;
    font-size: 75%;
    margin-bottom: 0px;
}
.bottomPaddings{
    display:flex;
    position: fixed;
    bottom: 0;
    width: 75%;
    text-align: center;
}

.paddingTopFooter{
    margin-top:15%;
}

@media only screen and (max-width: 600px) {
    .bottomPaddings{
        position: relative;
    }
}
.dollarSymbol{
    font-size: 8vw;
    font-weight: bold;
    display: flex;
    padding-top: 12vw;
    margin-left: 5vh;
}

.priceBig{
    font-size: 28vw;
    font-weight: bold;
}

.priceSmall{
    font-size: 6vw;
    font-weight: bold;
    padding-top: 11vw;
}

.priceText{
    font-size: 4vw;
    font-weight: bold;
}

@media only screen and (max-width: 600px) {
    .priceBig{
        font-size: 25vh;
        margin-bottom: 0px;
    }

    .dollarSymbol{
        font-size: 12vw;
        padding-top: 20vw;
        margin-left: 10vh;
        margin-bottom: 0px;
    }

    .priceSmall{
        font-size: 8vw;
        padding-top: 18vw;
    }

    .priceText{
        font-size: 6vw;
        margin-left: 0;
        margin-bottom: 0px;
    }
}
.textBig{
    font-size: 3vw;
    font-weight: bold;
}

.topPadding{
    padding-top: 10vw;
}

.discountText{
    color: #ff4c4c;
}

.buttonStyling{
    background-color: #ff4c4c !important;
    color: white;
    font-weight: bold;
    text-align: center;
    font-size: 110% auto;
    padding: 3%;
    width: 65%;
    margin-top: 5%;
}

@media only screen and (max-width: 600px) {
    .mobileCenter{
        text-align: center;
    }
    .textBig{
        font-size: 150%;
        font-weight: bold;
    }
    .topPadding{
        padding-top: 0vw;
    }
}
.triangleColor{
    color:#ff4c4c;
    font-size: 125%;
    margin-top:2.5%;
    margin-left:3%;
}

@media only screen and (max-width: 600px) {
    .triangleColor{
        font-size: 75%;
    }
    .mobileStyling{
        margin-left: 12%;
    }
}
.countdownNumbers{
    font-size: 150%;
    font-weight: bold;
    margin-bottom: 0;
    text-align: center;
}

.countdownText{
    font-size:75%;
    font-weight: bold;
    text-align: center;
}

@media only screen and (max-width: 600px) {
    .countdownText{
        font-size:50%;
    }

    .countdownNumbers{
        font-size: 100%;
    }
}
